 .footer-bottom {
     border-top: 1px solid #ffffff21;
     padding: 22px 0;
     text-align: center;
     opacity: 0.8;


     @media screen and (min-width: $w992) {
         display: flex;
         justify-content: space-between;
         align-items: center;
     }
 }

 .footer-copyright {
     padding-bottom: 15px;
     text-align: center;
     margin: 0 auto;
     color: var(--text-03);

     @media screen and (min-width: $w992) {
         padding-bottom: 0;
     }
 }

 .footer.footer--personal {

     & .container {
         max-width: 100%;
     }

     & .footer-copyright {
         @media screen and (min-width: $w1200) {
             margin-left: 288px;
         }
     }
 }